$output-bourbon-deprecation-warnings: false;
@import "bourbon";
@import "src/_fonts";
@import "src/_func";
@import "lib/slick";
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Source+Sans+Pro:400,700');

ol, ul {
	list-style: none; }

blockquote, q {
	quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

html, body {
	height: 100%; }

body {
	height: 100%;
	margin: 0;
	padding: 0;
	background: $bg;
	@include sans;
	font-size: 14px; }

@import "src/_preloader";
