ul {
	padding: 0;
	margin: 0; }

a {
	color: #6a6a6a;
	text-decoration: none;
	&:hover,
	&:active,
	&.is-active {
		color: $main; } }

button {
	padding: 0; }

.h-100 {
	height: 100% !important; }

.w-100 {
	width: 100% !important; }

.hero {
	height: 100%;
	z-index: 1;
	overflow: hidden;
	@include pr;
	min-height: $hero-min-height;
	&:hover {
		.hero__arrow {
			background: #706863;
			&:before {
				color: #3c3530 !important; }
			&:hover {
				background: $main;
				&:before {
					color: $w !important; } } }
		.hero__prev {
			left: $hero-arrow-gutter; }
		.hero__next {
			right: $hero-arrow-gutter; } }

	&:before {
		@include c;
		@include pa;
		left: 0;
		right: 0;
		bottom: 0;
		height: 150px;
		background-image: url('../images/hero-bottom.svg');
		z-index: 3;
		width: 100%;
		background-position: top;
		background-size: 100% 100%;
		text-align: center;
		@include rh(700) {
			display: none; }
		@include r(768) {
			display: none; } }
	> {
		.container {
			@include pr; } }
	&__top {
		@include pa;
		left: 0;
		right: 0;
		top: 40px;
		z-index: 4; }
	&__logo {
		display: block;
		.cls-2 {
			fill: #4a4c55;
			font-size: 36px;
			font-weight: 700; }
		.cls-1 {
			fill: $w;
			font-size: 36px; }
		.cls-3 {
			fill: #f1ebe7;
			font-size: 36px; }
		@include r(769) {
			width: 130px;
			margin: 0 auto; } }
	&__menu {
		text-align: right;
		&.is-active {
			opacity: 1;
			visibility: visible;
			li {
				transform: translate(0, 0);
				opacity: 1; } }

		@include r(769) {
			opacity: 0;
			visibility: hidden;
			@include pf;
			top: 0;
			left: 0;
			right: 0;
			background: rgba($b, .9);
			text-align: center;
			height: 100%;
			max-height: 100vh;
			overflow-y: auto;
			padding: 15px;
			transition: opacity $t, visibility $t;
			li {
				display: block;
				margin: 0;
				transform: translate(0, -5px);
				opacity: 0;
				transition: transform $t, opacity $t;
				transition-delay: .3s;
				a {
					color: $w;
					transition: background $t, color $t;
					&:hover {
						background: rgba($main, .5);
						color: $w; }
					&:before {
						display: none; } } } }
		li {
			display: inline-block;
			margin-left: 59px;
			@include fm;
			a {
				display: block;
				@include lh(60, 14);
				@include pr;
				text-transform: uppercase;
				color: $content-bg;
				letter-spacing: 0;
				&:before {
					@include c;
					@include pa;
					left: 0;
					right: 0;
					bottom: 0;
					height: 1px;
					background: $main;
					opacity: 0;
					visibility: hidden;
					transition: opacity $t, visibility $t, transform $t;
					transform: translate(0, -2px); }
				&:hover,
				&.is-active {
					color: $main;
					&:before {
						opacity: 1;
						visibility: visible;
						transform: translate(0, 0); } }
				&.is-active {
					@include sansb; } } } }
	&__item {
		height: 100% !important;
		// background-size: cover
		// background-repeat: no-repeat
		// background-position: 50% 50%
		min-height: $hero-min-height;
		@include pr;
		color: $w;
		text-align: center;
		span {
			display: block; }
		&:after {
			@include c;
			@include pa;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 1;
			background: $b;
			opacity: .7; }
		> {
			.container {
				@include pr;
				z-index: 2; } } }
	&__bg {
		@include pa;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		img {
			width: 100%;
			background-size: cover;
			height: 100%;
			transform: scale(1.2);
			transition: transform 1s ease-out; } }
	&__slider {
		z-index: 1;
		@include pr;
		height: 100%;
		min-height: $hero-min-height;
		.slick-active {
			.hero__bg {
				img {
					transform: scale(1);
					transition: transform 1s ease; } } } }
	&__prev,
	&__next {
		@include pa;
		top: 50%;
		transform: translate(0, -50%);
		z-index: 2;
		// opacity: 0
 }		// visibility: hidden
	&__prev {
		left: $hero-arrow-gutter - 10;
		@include r(992) {
			left: 0; }
		&:before {
			top: -2px;
			left: -4px; } }
	&__next {
		right: $hero-arrow-gutter - 10;
		@include r(992) {
			right: 0; }
		&:before {
			transform: rotate(180deg);
			right: -2px;
			top: 1px; } }
	&__arrow {
		// background: #706863
		@include qd(45);
		border-radius: 2px;
		line-height: 45px;
		text-align: center;
		// transition: background $t, color $t, opacity $t, visibility $t, left $t, right $t
		transition: background $t, color $t, left $t, right $t;
		@include r(769) {
			display: none; }
		&:hover {
			background: $main;
			&:before {
				color: $w; } }
		&:before {
			@include pr;
			@include fa;
			display: block;
			content: '\f104';
			// color: #3c3530
			color: shade($w, 30%);
			font-size: 41px;
			transition: background $t, color $t; } }
	&__info {
		span {
			display: block; }
		.line {
			margin: $line-margin auto;
			@include r(992) {
				margin: $line-margin/2 auto; } } }
	&__title {
		&-top {
			font-size: 24px;
			@include source;
			@include r(770) {
				@include lh(35, 24);
				margin-bottom: 20px; } }
		&-bottom {
			font-size: 72px;
			@include sansb;
			text-transform: uppercase;
			@include lh(101, 72);
			@include r(770) {
				font-size: 40px;
				@include lh(50, 40); } } }
	&__text {
		color: #cbc6c0;
		@include lh(24, 14);
		display: block;
		max-width: 590px;
		margin: 0 auto 71px;
		@include r(769) {
			display: none; }
		p {
			margin: 0; } }
	&__buttons {
		a,
		button {
			margin-left: 29px;
			@include fm;
			@include r(577) {
				display: block;
				width: 100%;
				margin: 0;
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0; } } } }
	&__anchor {
		&-border {
			@include pa;
			bottom: -10px;
			left: 50%;
			transform: translate(-50%, 0);
			@include rh(700) {
				bottom: 20px; }
			@include r(769) {
				bottom: 20px; } } }
	.btn {
		&.transparent {
			&:hover {
				background-color: $main;
				border-color: transparent;
				color: $w; } }
		&.fill {
			&:hover {
				background-color: transparent;
				border-color: $w;
				color: $w; } } }
	.slick-list,
	.slick-track {
		height: 100% !important;
		min-height: $hero-min-height;
		transition: all 1s cubic-bezier(0.7, 0, 0.3, 1); }

	.slick-slide {
		> {
			div {
				height: 100%;
				min-height: $hero-min-height; } } } }

.slick-slider {
	-webkit-user-select: text;
	-khtml-user-select: text;
	-moz-user-select: text;
	-ms-user-select: text;
	user-select: text; }

.main {
	&__title {
		@include sourceb;
		font-size: 36px;
		text-transform: uppercase;
		color: #6a6a6a;
		text-align: center; }
	&__icon {
		@include sq(64);
		background: $w;
		@include df;
		@include cc;
		svg {
			z-index: 99;
			@include pr;
			width: 22px;
			fill: $main; }
		&-border {
			@include df;
			@include cc;
			@include sq(74);
			background: rgba($w, .25);
			z-index: 3;
			outline: none; } }
	&__text {
		font-size: 14px;
		@include lh(29, 14);
		@include source;
		color: #999;
		@include r(769) {
			text-align: center; } } }

.center {
	text-align: center; }

.no-padding {
	padding-left: 0;
	padding-right: 0; }

.btn {
	min-width: 225px;
	height: 56px;
	line-height: 56px;
	text-align: center;
	text-transform: uppercase;
	color: $w;
	border-radius: 2px;
	&__shadow {
		box-shadow: 0 2px 4.8px 0.3px rgba(0, 0, 0, 0.25); }
	&__inline {
		display: inline-block; }
	&.transparent {
		border: 2px solid $w;
		transition: background $t, color $t, border-color $t; }
	&.fill {
		background-color: $main;
		border: 2px solid transparent;
		transition: background $t, color $t, border-color $t;
		&:hover {
			color: $w;
			background: darken($main, 10%); } }
	&.inactive {
		background-color: #999; } }


.mobile__btn {
	@include pf;
	top: 45px;
	right: 15px;
	width: 40px;
	height: 40px;
	z-index: 99;
	display: block;
	background-color: transparent;
	border: 0;
	cursor: pointer;
	outline: none;
	span {
		width: 30px;
		height: 2px;
		background: $main;
		@include pa;
		left: 50%;
		margin-left: -15px;
		top: 0;
		border-radius: 3px;
		transition: transform $t;
		&:nth-of-type(1) {
			top: 10px; }
		&:nth-of-type(2) {
			top: 50%;
			margin-top: -1px; }
		&:nth-of-type(3) {
			top: auto;
			bottom: 10px; } }
	&.is-active {
		span {
			&:nth-of-type(2) {
				opacity: 0;
				visibility: hidden;
				transform: scale(0); }
			&:nth-of-type(1) {
				transform: rotate(-45deg);
				top: 18px; }
			&:nth-of-type(3) {
				transform: rotate(45deg);
				bottom: 20px; }
			&:nth-of-type(1),
			&:nth-of-type(3) {} } }

	@include rmin(769) {
		display: none; } }

.line {
	display: block;
	@include sq(7);
	background: $main;
	@include pr;
	margin: 0 auto;
	&:before,
	&:after {
		@include c;
		@include pa;
		width: 81px;
		height: 1px;
		background: $w;
		top: 50%; }
	&:before {
		left: 21px; }
	&:after {
		right: 21px; }
	&__white {
		&:before,
		&:after {
			background: $w; } }
	&__grey {
		&:before,
		&:after {
			background: #999; } } }

section {
	padding: $section-gutter 0 0;
	@include r(769) {
		padding: $section-gutter/2 0 0; } }

.sub__title {
	text-transform: uppercase;
	font-size: 18px;
	color: #6a6a6a;
	@include sansb;
	@include pr;
	text-align: center;
	&:before {
		@include c;
		@include pa;
		left: 50%;
		transform: translate(-50%, 0);
		background: #6a6a6a;
		width: 41px;
		height: 1px;
		bottom: -15px;
		box-shadow: 0px 1px 0px 0px rgba(225,225,225,1); } }

.line__title {
	font-size: 36px;
	text-transform: uppercase;
	@include sourceb;
	color: #6a6a6a;
	@include pr;
	&:before {
		@include c;
		@include pa;
		left: 0;
		width: 6px;
		height: 100%;
		background: $main; }
	&-left {
		padding-left: 35px; }
	&-right {
		text-align: right;
		padding-right: 35px;
		&:before {
			left: auto;
			right: 0; } } }

.center-text {
	text-align: center; }

.whoweare {
	@include pr;
	&:before {
		@include c;
		@include pa;
		bottom: -$section-gutter/2;
		left: 0;
		right: 0;
		height: 1px;
		background: #e6e7eb;
		@include r(769) {
			bottom: -$section-gutter/4; } }
	&__item {
		text-align: center;
		@include r(769) {
			margin-bottom: $mobile-margin-bottom; }
		&:hover {
			> {
				.whoweare__icon {
					transform: rotate(45deg);
					border-color: $main;
					&:before {
						transform: rotate(-45deg); } }
				.whoweare__title {
					color: $main; } } } }
	&__icon {
		@include qd(73);
		border: 1px solid #e5e5e5;
		@include df;
		@include cc;
		margin: 0 auto 26px;
		transition: transform $t;
		border-radius: 2px;
		&:before {
			@include c;
			display: block;
			transition: transform $t; }
		&.glass {
			&:before {
				@include sprite($whoweare1); } }
		&.heart {
			&:before {
				@include sprite($whoweare2); } }
		&.idea {
			&:before {
				@include sprite($whoweare3); } }
		&.dialog {
			&:before {
				@include sprite($whoweare4); } } }
	&__title {
		margin-bottom: 30px;
		transition: color $t; } }

.about {
	&__monitor {
		img {
			width: 100%; }
		@include r(769) {
			margin-bottom: $mobile-margin-bottom; } } }

.history {
	&__text {
		margin-bottom: 50px; } }

.services {
	.slick-active {
		.services__image {
			transform: translate3d(0, 0, 0);
			opacity: 1;
			visibility: visible; } }
	&__title {
		color: $w;
		margin-bottom: 82px; }
	&__name {
		text-transform: uppercase;
		color: $main;
		font-size: 18px;
		@include sansb;
		@include r(992) {
			text-align: center; } }
	&__item {
		background: #202020;
		overflow: hidden; }
	&__info {
		text-align: right;
		max-width: 800px;
		margin-left: auto;
		padding: $services-padding 74px;
		@include r(992) {
			padding: $services-padding/2 15px; } }
	&__image {
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		height: 100%;
		transform: translate3d(20%, 0, 0);
		opacity: 0;
		visibility: hidden;
		transition: transform 1s ease-in, opacity $t, visibility $t; }
	&__block {
		@include pr;
		margin-bottom: $mobile-margin-bottom;
		.main__icon {
			transition: transform .5s linear;
			transform: rotate(0deg) scale(1); }
		&:hover {
			> {
				.main__icon-border {
					> {
						.main__icon {
							transform: rotate(360deg) scale(1.1); } } } } } }
	&__icon {
		svg {
			width: 30px; }
		&-border {
			@include pa;
			top: 0;
			right: -112px;
			@include r(992) {
				@include pr;
				right: auto;
				margin: 0 auto; } } }
	&__slider {
		@include pr;
		> {
			.slick-dots {
				@include pa;
				top: 50%;
				left: 49px;
				li {
					margin-bottom: 11px;
					button {
						font-size: 0;
						border: 0;
						width: 1px;
						height: 34px;
						background: #3e3e3e;
						cursor: pointer;
						outline: none;
						@include pr;
						&:before {
							@include c;
							@include pa;
							width: 20px;
							height: 44px;
							top: -5px;
							left: -9px; } }
					&.slick-active {
						button {
							width: 2px;
							background: $main; } } }
				@include r(992) {
					@include pa;
					top: 0;
					left: 50%;
					transform: translate(-50%, 0);
					li {
						display: inline-block;
						margin-bottom: 0;
						margin-right: 11px;
						&:last-child {
							margin-right: 0; }
						button {
							width: 34px;
							height: 1px;
							&:before {
								width: 44px;
								height: 20px;
								top: -9px;
								left: -5px; } }
						&.slick-active {
							button {
								width: 34px; } } } } } } } }

.portfolio {
	&__pagination {
		text-align: center;
		margin: 60px 0 45px;
		a {
			display: inline-block;
			margin-left: 41px;
			@include fm;
			@include pr;
			color: #999;
			&:before {
				content: "/";
				@include pa;
				left: -20px; }
			&:first-child {
				&:before {
					display: none; } }
			&.is-active {
				color: $main;
				@include sansb;
				&:before {
					color: #999;
					font-weight: 400; } }
			&:hover {
				color: $main; }
			@include r(992) {
				margin-bottom: 10px;
				&:before {
					display: none; } } } }
	&__item {
		@include pr;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		&-shuffle {
			// float: left
			width: 25%; }
		> {
			a.look__all {
				display: block;
				height: 480px;
				@include pr;
				&:before {
					@include c;
					@include pa;
					background: rgba($main, .80);
					top: 0;
					bottom: 0;
					left: 0;
					right: 0;
					opacity: 0;
					visibility: hidden;
					transition: opacity $t, visibility $t; }
				&:hover {
					&:before {
						opacity: 1;
						visibility: visible; }
					> {
						.portfolio__info {
							opacity: 1;
							visibility: visible; } } }
				span {
					display: block; } } } }
	&__info {
		@include pa;
		bottom: 0;
		left: 0;
		padding: 0 50px 50px;
		opacity: 0;
		visibility: hidden;
		transition: opacity $t, visibility $t; }
	&__name,
	&__category {
		color: $w; }
	&__name {
		font-size: 24px;
		@include sourceb;
		text-transform: uppercase;
		line-height: 1.4;
		@include pr;
		&:before {
			@include c;
			@include pa;
			height: 4px;
			width: 44px;
			background: $w;
			top: -31px;
			left: 0; } }
	&__category {
		font-size: 14px; } }

.study {
	background: $content-bg;
	padding-bottom: $section-gutter;
	@include pr;

	&__image {
		background-position: 50% 50%;
		background-repeat: no-repeat;
		background-size: cover;
		height: 100%;
		margin-left: -40px;
		transform: translate(-40px, 0);
		transition: transform $t;
		@include r(992) {
			margin-left: 0;
			height: 500px; } }
	.slick-active {
		.study__image {
			transform: translate(0, 0);
			transition: transform $t; } }
	&__info {
		background: $w;
		box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
		padding: 80px 30px;
		margin: 50px 0;
		margin-right: -40px;
		z-index: 1;
		@include pr;
		p {
			margin: 30px 0; }
		@include r(992) {
			margin-right: 0;
			margin-bottom: 0;
			margin-top: 0; } }
	&__item {
		.container {
			max-width: 100%; } }
	&__icon {
		transform: rotate(45deg);
		@include qd(64);
		background: $main;
		@include df;
		@include cc;
		margin: 0 auto 50px;
		&.idea-white {
			&:before {
				@include c;
				@include pr;
				display: block;
				transform: rotate(-45deg);
				@include sprite($idea-white); } } }
	&__wrapper {
		@include pr;
		@include r(992) {
			max-width: 100%; } }
	&__dots {
		@include pa;
		bottom: 65px;
		width: $dots-calc;
		margin-left: 30px;
		height: 12px;
		z-index: 3;
		// left: calc(50% + -120px)
		// margin-left: -25%
		// +df
		// +cg
		@include r(992) {
			left: 50%;
			transform: translate(-50%, 0);
			margin-left: 0;
			text-align: center;
			// margin-left: -$dots-calc/2
			bottom: auto;
			top: 20px; }
		ul {
			li {
				display: inline-block;
				margin-left: 8px;
				@include fm;
				button {
					font-size: 0;
					@include sq(12);
					background: $content-bg;
					border: 0;
					cursor: pointer;
					outline: none;
					@include r(992) {
						background: shade($content-bg, 20%); } }
				&.slick-active {
