body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #fff;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
}

html.js .loader {
  background: none repeat scroll 0 0 #fff;
  bottom: 0;
  height: 100%;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 9999;
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
}

html.js .loader--hidden {
  opacity: 0;
  visibility: hidden;
}

html.js .loader_inner {
  background-color: #fff;
  height: 60px;
  width: 60px;
  margin-top: -30px;
  margin-left: -30px;
  left: 50%;
  top: 50%;
  position: absolute;
}

.preloader {
  position: relative;
  display: inline-block;
}

.preloader span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 99;
  font-size: 30px;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 700;
  color: #fff;
  display: block;
}

.preloader .main__icon-border {
  background: rgba(231, 76, 60, 0.5);
  animation: preloader__bino 2s reverse infinite;
}

.preloader .main__icon {
  background: #e74c3c;
  animation: preloader__bino 1s linear infinite;
}

@keyframes fade__bino {
  from {
    transform: translate3d(0, 10px, 0);
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.fade__bino {
  animation-name: fade__bino;
  animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

@keyframes fadeInLeftSlow {
  from {
    opacity: 0;
    transform: translate3d(-50px, 0, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInLeftSlow {
  animation-name: fadeInLeftSlow;
}

@keyframes preloader__bino {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
  to {
    transform: scale(1);
  }
}

.preloader__bino {
  animation-name: preloader__bino;
  animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

